import { Component, OnInit } from '@angular/core';
import { ImageService } from 'src/services/image.service';
import { Router } from '@angular/router';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-review',
  templateUrl: './review.component.html',
  styleUrls: ['./review.component.css']
})
export class ReviewComponent implements OnInit {

  img: any ={};
  comment: string;
  constructor(private imgInfo: ImageService, private router : Router) { }

  ngOnInit() {
    if(this.imgInfo.getImg()){
      this.img = this.imgInfo.getImg();
    }else{
      this.router.navigate(['home']);
    }
  }

  starList: boolean[] = [true, true, true, true, true, true, true, true, true, true];       // create a list which contains status of 5 stars
  rating: number;
  //Create a function which receives the value counting of stars click, 
  //and according to that value we do change the value of that star in list.
  setStar(data: any) {
    this.rating = data + 1;
    for (var i = 0; i <= 9; i++) {
      if (i <= data) {
        this.starList[i] = false;
      }
      else {
        this.starList[i] = true;
      }
    }
  } 
  
  reviewSubmit(){
  var flickrData: any = {
    id : this.imgInfo.getImg()['id'],
    comment : this.comment,
    rating : this.rating
  }
   localStorage.setItem('flickrStorage', JSON.stringify(flickrData));
    this.router.navigate(['home']);

}
}

