import { Component } from '@angular/core';
import { FlickrService } from 'src/services/flickr.service';
import { PageEvent } from '@angular/material';
import { ImageService } from 'src/services/image.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent {

  imgUrl: string;
  p: number = 1;
  img: any = [];
  pageIndex: number = 0;
  length: number;
  highValue: number = 20;
  pageSize = this.highValue;
  pageSizeOptions: number[] = [5, 10, 25, 100];
  lowValue: number = 0;
  pageEvent: PageEvent;
  ratingAny : any;

  pageFn() {
    return this.pageSize;
  }

  constructor(private api: FlickrService, private imgInfo: ImageService, private router : Router) {
    this.api.invokeApi()
      .subscribe((res) => {
        this.length = res.photos.total;
        this.img = res.photos.photo;
        // if(JSON.parse(localStorage.getItem('flickStorage'))){
        this.ratingAny = JSON.parse(localStorage.getItem('flickrStorage'));
        // }
      });
  }

  getPaginatorData(event) {
    if (event.pageIndex == this.pageIndex + 1) {
      this.lowValue = this.lowValue + this.pageSize;
      this.highValue = this.highValue + this.pageSize;
    }
    else if (event.pageIndex == this.pageIndex - 1) {
      this.lowValue = this.lowValue - this.pageSize;
      this.highValue = this.highValue - this.pageSize;
    }
    this.pageIndex = event.pageIndex;
  }

  openReviewPage(id){
    this.imgInfo.imgInfo(id)
    this.router.navigate(['review'])
  }



}
